.js-drawer-icon {
  position: fixed;
  top: 22px;
  right: 20px;
  z-index: 40;
  display: block;
  transition: top ease 0.3s;
}
@media screen and (min-width: 768px) {
  .js-drawer-icon {
    top: 42px;
    right: 25px;
  }
}
@media screen and (min-width: 1024px) {
  .js-drawer-icon {
    display: none;
  }
}
.js-drawer-icon.is-active {
  transition: transform 0.3s ease 0s;
  right: 75%;
}
.js-drawer-icon.is-active .js-drawer-icon__bar1 {
  transition: transform 0.3s ease 0s;
  top: 9.9px;
  transform: rotate(45deg);
  background-color: #FFF;
}
.js-drawer-icon.is-active .js-drawer-icon__bar2 {
  transition: transform 0.3s ease 0s;
  top: 9.9px;
  display: none;
}
.js-drawer-icon.is-active .js-drawer-icon__bar3 {
  transition: transform 0.3s ease 0s;
  top: 9.9px;
  transform: rotate(-45deg);
  background-color: #FFF;
}
@media screen and (min-width: 768px) {
  .js-drawer-icon.change-large {
    top: 32px;
  }
}

/* barがはいる箱 */
.js-drawer-icon__bars {
  width: 27px;
  height: 16px;
  display: block;
  position: relative;
}

/* それぞれのbarの線分を作る */
.js-drawer-icon__bar1,
.js-drawer-icon__bar2,
.js-drawer-icon__bar3 {
  width: 27px;
  height: 1px;
  background-color: #FFF;
  position: absolute;
  top: 0;
  left: 0;
  transition: background-color ease 0.3s;
}
.js-drawer-icon__bar1.change-large,
.js-drawer-icon__bar2.change-large,
.js-drawer-icon__bar3.change-large {
  background-color: #1B1919;
}

.js-drawer-icon__bar1 {
  top: 0;
}

.js-drawer-icon__bar2 {
  top: 8px;
}

.js-drawer-icon__bar3 {
  top: 16px;
}

