/* 文字と線のレイアウト */
.journal__view-more {
  display: flex;
  align-items: center;
}

/* 線 */
.journal__line {
  position: relative;
  width: 20vw;
  height: 1px;
  background: transparent;
}
.journal__line::before {
  width: 20vw;
  height: 1px;
  background: #fff;
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  /*線の動き*/
  animation: lineMove 3.5s ease-in-out infinite, lineMoveHide 3.5s ease-out infinite;
}

/* 線の変形 */
@keyframes lineMove {
  0% {
    width: 0;
  }
  100% {
    width: 20vw;
  }
}
/* 線の透過 */
@keyframes lineMoveHide {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    opacity: 0.9;
  }
  100% {
    opacity: 0;
  }
}
.main {
