}

/* スライドのスタイル */
.slide {
  position: relative;
}

.slide__img {
  height: 100vh;
}
.slide__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.slide__title {
  font-size: 1.25rem;
  line-height: 1.6;
  color: #fff;
  position: absolute;
  right: 8%;
  bottom: 15%;
  text-align: right;
}
@media screen and (min-width: 768px) {
  .slide__title {
    font-size: 2.5rem;
  }
}

/* スライドのアニメーション */
@keyframes slideZoom {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}
/* -active時にアニメーションする */
.swiper-slide-active .slide__img,
.swiper-slide-duplicate-active .slide__img,
.swiper-slide-prev .slide__img {
  animation: slideZoom 10s linear 0s normal both;
}

/* タイトルのアニメーション */
@keyframes mainFadeIn {
  0% {
    transform: scale(0.5);
    opacity: 0;
    filter: blur(300px);
  }
  100% {
    transform: scale(1);
    opacity: 1;
    filter: blur(0);
  }
}
/* -active時にアニメーションする */
.swiper-slide-active .slide__title,
.swiper-slide-duplicate-active .slide__title,
.swiper-slide-prev .slide__title {
  animation: mainFadeIn 2s 0.5s ease both;
}

/* ページネーション */
.swiper-container {
  position: relative;
}

/* レイアウト */
.swiper-pagination {
  transform: rotate(90deg);
  position: absolute;
  top: 40%;
  left: 50%;
}

/* スタイル */
.swiper-pagination-bullet {
  width: 1.5rem;
  height: 1px;
  background: #fff;
  transition: 3s cubic-bezier(0.2, 1, 0.2, 1);
  border-radius: 0;
  opacity: 0.7;
}
@media screen and (min-width: 600px) {
  .swiper-pagination-bullet {
    width: 3rem;
    height: 2px;
  }
}

/* -active時にアニメーションする */
.swiper-pagination-bullet-active {
  width: 5rem;
  height: 1px;
  background: #fff;
  opacity: 1;
}
@media screen and (min-width: 600px) {
  .swiper-pagination-bullet-active {
    width: 6rem;
    height: 2px;
  }
}

/* 位置調整 */
.swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 0.2rem;
}
@media screen and (min-width: 600px) {
  .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
    margin: 0 0.3rem;
  }
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  height: 1px;
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  left: -38%;
}
